.button{
  height: 44px;
  width: 218px;
  cursor: pointer;
  background: transparent;
  border: solid 1px rgba(5, 62, 94, .4);
  font-size: 16px;
  text-align: center;
  color: #053e5e;
  &:hover{
    border-color: #E64446;
    color: #E64446;
  }
  &_large{
    height: 52px;
    border-color: rgba(5, 62, 94, 1);
    font-size: 16px;
  }
  &__primary { 
    border: 0;
    background-color: #e64446;
    color: #fff;
    font-weight: 600;
    &:hover{
      background-color: #ce3d3e;
    }
    &:focus{
      background-color: #b83638;
    }
  }
  &_full{
    width: 100%;
  }
}
.primary-btn{
  background-color: #e64446;
  color: #fff;
}
.primary-btn:hover{
  background-color: #ce3d3e;
}
.primary-btn:focus{
  background-color: #b83638;
}