@import "../../css/colors.scss";
.box-white {
  min-height: 92px;
  box-shadow: 0 8px 20px 0 rgba(0, 0, 0, 0.02);
  background-color: #fff;
  padding: 14px 24px 26px 24px;
  margin-bottom: 16px;
}
.comming-soon{
  display: flex;
  justify-content: center;
  align-items: center;
  color: $marine;
  margin-top: 16px;
  text-align: center;
  max-width: 1256px;
  @media screen and (min-width: 1844px)  {
    max-width: 1576px;
  }
  &__text{
    font-size: 12.5px;
    color: rgba(5, 62, 94, .67);
  }
  &__title{
    font-size: 18px;
  }
}
.invest-list{
  display: flex;
  flex-wrap: wrap;
  margin-left: -16px;
  &__item{
    width: 302px;    
    margin-left: 16px;
  }
  &__description{
    color: rgba($marine, .67);
    font-size: 12.5px;
    text-align: center;
  }
  &__logo{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100px;
  }
  &__name{
    text-align: center;
    font-size: 18px;
    margin-bottom: 8px;
  }
  &__country{
    color: #90a9b8;
    padding-left: 5px;
  }
}
.invest-list-table {
  border-top: 1px solid #9bb2bf;
  padding-top: 10px;
  padding-bottom: 14px;
  margin-top: 24px;
  &__row {
    display: flex;
    padding: 10px 0;
  }
  &__col {
    text-align: left;
    flex: 1;
    &:first-child{
      color: #688a9e;
    }
    &:last-child{
      text-align: right;
    }
  }
}
.container-responsive{
  margin: 0 24px;
}