@import "./css/colors.scss";
*,
*::before,
*::after {
  box-sizing: border-box;
}

ul,
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
p,
ul,
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}
html {
  height: 100%;
}
#root{
  height: 100%;
}
body {
  height: 100%;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #053e5e;
  background-color: #fafafa;
}

ul,
ol[class] {
  list-style: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img {
  max-width: 100%;
  display: block;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}
a, a:hover, a:focus{
  text-decoration: none;
  color: inherit;
}
.hidden{display: none;};
@import "./css/bnt.scss";
.group-main{
  display: flex;
  height: 100%;
}
.navbar--top{
  height: 81px;
  background-color: #fafafa;
  width: 100%;
  position: relative;
  left: 0;
  top: 0;
}
.navbar--top-line{
  height: 2px;
  opacity: 0.1;
  background-color: #053e5e;
  position: absolute;
  width: 100%;
  bottom: -2px;
  z-index: 1;
}
.navbar--top-right{
  display: flex;
  align-items: center;
  z-index: 1;
}
.display-flex-end {
  display: flex;
  justify-content: flex-end;
}
.container {
  width: 920px;
  min-width: 920px;
  margin: 0 auto;
  height: inherit;
}
.logout{
  font-size: 14px;
  color: #e64446;
  line-height: 22px;
  margin-left: 30px;
}
.logout a{
  line-height: 22px;
  display: inline-block;
  vertical-align: middle;
}
.logout .ico{
  line-height: 22px;
  vertical-align: middle;
}
.logout .ico:before{
  font-size: 22px;
  line-height: 22px;
  margin: 0 8px 0 0;
}
.notification {
  width: 36px;
  height: 36px;
  position: relative;
}
.notification--description {
  width: 732px;
  height: 64px;
  background-color: #ffffff;
  position: absolute;
  left: -747px;
  top: 0;
  padding-left: 33px;
  padding-top: 14px;
  color: #053e5e;
  top: 50%;
  margin-top: -32px;
}
.notification--description-title {
  font-size: 15px;
  line-height: 22px;
}
.notification--description-text {
  font-size: 12.5px;
}
.notification--description-shadow {
  position: absolute;
  bottom: 0;
  width: 90%;
  height: 7px;
  opacity: 0.3;
  -webkit-filter: blur(10px);
  filter: blur(10px);
  background-color: #000000;
  z-index: -1;
  left: 50%;
  margin-left: -45%;
}
.notification--oval {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;

}
.notification--oval .icon-bell-ic{
  font-size: 20px;
  color: #053e5e;
}
.notification--oval .icon-bell-ic:before{
  font-size: 22px;
  margin: 0;
}
.notification--read-btn {
  font-size: 14px;
  position: absolute;
  cursor: pointer;
  top: 0;
  right: 0;
  width: 85px;
  height: 100%;
  background-color: #fdeced;
  display: flex;
  justify-content: center;
  align-items: center;
}
.notification--arrow-left {
  border: 10px solid transparent;
  border-left-color: #fdeced;
  border-right: 0;
  position: absolute;
  right: -10px;
  top: 50%;
  margin-top: -10px;
}
.notification--read-btn_text{
  color: #e64446;
}
.navbar--line {
  width: 100%;
  height: 2px;
  opacity: 0.3;
  background-color: #f0f0f0;
  position: absolute;
  left: 0;
  top: 81px;
}
.navbar--left{
  position: relative;
  display: flex;
  left: 0;
  top: 0;
  min-height: 100%;
  background-color: #fbfbf8;
  z-index: 1;
}
.navbar--left .main-menu {
  width: 80px;
  position: relative;
}
.main-menu--settings{
  position: absolute;
  bottom: 0;
  left: 0;
}
.navbar--left .main-menu li a{
  display: flex;
  width: 80px;
  height: 80px;
  align-items: center;
  justify-content: center;
}
.navbar--left .main-menu li a .main-menu-text{
  display: none;
}
.navbar--left .main-menu li a .icon {
  color: #053e5e;
  font-size: 24px;
  opacity: 0.2;
}
.navbar--left .main-menu li a .icon.logo{
  font-size: 30px;
}
.navbar--left .main-menu li a .icon.icon-logo-ic {
  opacity: 1;
}
.navbar--left .main-menu li a.active {
  background-color: rgba(201, 212, 217, .1);
}
.navbar--left .main-menu li a:hover .icon {
  opacity: .6;
}
.navbar--left .main-menu li a:hover{
  background-color: rgba(245, 246, 244, .5);
}
.navbar--left .main-menu li a.active .icon{
  color: #e64446;
  opacity: 1;
}
.navbar--left .sub-menu {
  width: 360px;
  background: #fff;
  display: none;
  overflow: hidden;
}
.scroll-y{
  padding: 0 17px;
  overflow-x: hidden;
  overflow-y: auto;
  width: 360px;
  height: calc(100% - 82px);
}
.navbar--left .sub-menu.active{
  display: block;
}
.sub-menu--total-sum{
  height: 124px;
  text-align: center;
}
.navbar--left .sub-menu--name{
  height: 82px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  color: #053e5e;
  position: relative;
}
.navbar--left .sub-menu--name .hover-submenu-bg{
  position: absolute;
  left: 0;
  top: 80px;
  height: 200vh;
  right: 0;
  -webkit-backdrop-filter: blur(2px);
  backdrop-filter: blur(2px);
  background-color: rgba(0, 0, 0, 0.09);
  z-index: 1;
  display: none;
}
.navbar--left .sub-menu--name .hover-submenu{
  width: 100%;
  background-color: #ffffff;
  z-index: 1;
}
.navbar--left .sub-menu--name:hover .hover-submenu-bg{
  display: block;
}
.navbar--left .sub-menu--name .hover-submenu a {
  display: block;
  height: 70px;
  margin: 0 34px;
  line-height: 70px;
  font-size: 18px;
  opacity: 0.45;
}
.navbar--left .sub-menu--name .hover-submenu a.active{
  opacity: 1;
  background: 100% 50% url("img/checkbox-ic.svg") no-repeat;
}
.sub-menu--total-text{
  color: #9bb2bf;
  font-size: 14px;
  margin-top: 24px;
}
.total-count{
  font-size: 38px;
}
.total-count .currency{
  color: #9bb2bf;
}
.total-count .icon-gbr-ic:before{
  margin: 0;
}
.total-count--cent{
  font-size: 26px;
}
.navbar--left .sub-menu--name .icon-arrow-ic{
  font-size: 18px;
}
.sub-menu--label{
  margin: 4px 0 12px 16px;
  font-size: 16px;
  opacity: 0.6;
}
.container-fluid{
  width: 100%;
}
.container-content{
  overflow-y: auto;
  min-height: calc(100% - 82px);
  max-height: calc(100% - 82px);
}
.list-item_border {
  border: solid 3px rgba(5, 62, 94, 0.08);
}
.list-item_color-orange, .ico-bg_orange {
  background-color: #fff1e1;
}
.list-item_color-green, .ico-bg_green {
  background-color: #f7f7ed;
}
.list-item_color-yellow, .ico-bg_yellow {
  background-color: #fff4d1;
}
.list-item_color-pink, .ico-bg_pink {
  background-color: #fce7e7;
}
.list-item_color-ebe4eb, .ico-bg_ebe4eb {
  background-color: #ebe4eb;
}
.list-item_color-grey, .ico-bg_grey {
  background-color: #f3f3f3;
}

.sub-menu--list-item{
  width: 100%;
  height: 82px;
  margin-bottom: 8px;
  display: flex;
  position: relative;
  cursor: pointer;
}
.sub-menu--list-item:before{
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, .02);
  display: none;
}
.sub-menu--list-item:hover:before{
  display: block;
}
.list-item--currency{
  font-size: 28px;
  width: 67px;
  background-color: rgba(0, 0, 0, .02);
  display: flex;
  align-items: center;
  justify-content: center;  
  color: rgba(0, 0, 0, .2);
}
.list-item--info{
  margin: 15px 0 0 16px;
}
.list-item--sum{
  font-size: 24px;
}
.list-item--sum_cent{
  font-size: 18px;
}
.list-item--text {
  color: rgba(5, 62, 94, .4);
  font-size: 14px;
}
.sub-menu button {
  font-size: 16px;
  cursor: pointer;
  width: 100%;
  letter-spacing: 0.23px;
  text-align: center;
  color: #053e5e;
  height: 52px;
  background: transparent;
  border: solid 1px rgba(5, 62, 94, .2);
}
.info-card {
  background-color: #f7f7ed;
  margin-bottom: 16px;
  position: relative;
  padding-top: 30px;
}
.info-card--shadow {
  width: 100%;
  position: absolute;
  left: 0;
  z-index: -1;
  bottom: 0;
  height: 15px;
  opacity: 0.2;
  -webkit-filter: blur(10px);
  filter: blur(10px);
  background-color: #000000;
}
.title-calendar{
  display: flex;
  margin-bottom: 7px;
}
.title-calendar--text{
  width: 100%;
  font-size: 18px;
  font-weight: 600;
}
.title-box-text{
  width: 100%;
  font-size: 16px;
  font-weight: 600; 
  text-align: center;
}
.title-box-text_support {
  height: 43px;
  line-height: 43px;
}
.title-calendar .icon-calendar-ic{
  font-size: 26px;
}
.row-box{
  display: flex;
}
.white-box-shadow{
  box-shadow: 0 8px 20px 0 rgba(0, 0, 0, 0.02);
  background-color: #ffffff;
}
.row-box .row-box--right{
  width: 286px;
  min-width: 286px;
  margin-left: 24px;
}
.row-box .row-box--left{
  width: 100%;
  position: relative;
}
ul.cash-flow-list{
  margin: 13px 0 30px 0;
}
.cash-flow-list--item{
  height: 56px;
  background-color: #ffffff;
  padding: 9px 20px 0;
  display: flex;
}
.cash-flow-list--item-info{
  width: 100%;
  margin-left: 16px;
}
.cash-flow-list--item-name{
  display: flex;
  justify-content: space-between;
}
.arrow-cash-in, .arrow-cash-out{
  border: solid 1px #053e5e;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  font-size: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.arrow-cash-out{
  border: solid 1px #e64446;
  color: #e64446;
  transform: rotate(180deg);
}
.cash-flow-list--item-text {
  font-size: 12.5px;
  opacity: .45;
}
.price-cash-out{
  color: #e64446;
}
.calendar-date-label{
  font-size: 14px;
  margin-left: 21px;
  opacity: .6;
}
.support-container{
  padding: 0 23px 30px 23px;
  text-align: center;
}
.support-container img{
  display: inline-block;
}
.support-line{
  height: 1px;
  width: 100%;
  object-fit: contain;
  background-color: #9bb2bf;
  margin: 24px 0;
}
.support-user-name {
  margin-top: 12px;
  font-size: 22px;
  font-weight: 600;
}
.support-user-label{
  font-size: 15px;
  color: #9bb2bf;
}
.support-phone {
  font-size: 22px;
}
.support-email {
  font-size: 16px;
  color: #9bb2bf;
  margin-bottom: 30px;
}
.red-card {
  height: 91px;
  overflow: hidden;
}
.local-label{
  font-size: 12.5px;
  opacity: .45;
}
.local-text{
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
}
.local-table{
  width: 100%;
}
.local-table--row{
  display: flex;
  padding-bottom: 16px;
}
.local-table--col {
  width: 50%;
}
.tabs-nav{
  border-bottom: 1px solid  rgba(155, 178, 191, .3);
  margin-bottom: 27px;
  height: 30px;
  display: flex;
}
.tabs-nav .tab {
  color: #9bb2bf;
  font-size: 15px;
  min-width: 92px;
  text-align: center;
  padding: 0 16px;
  cursor: pointer;
  position: relative;
  bottom: -1px;
}
.tabs-nav .tab.active{
  border-bottom: 1px solid #e64446;
  color: #e64446;
}
.tabs-nav .tab:not(.active):hover {
  color: #053e5e;
}
.total-count_balance{
  font-size: 34px;
}
.total-count_balance .total-count--cent{
  font-size: 24px;
}
.balance-label{
  font-size: 14px;
  opacity: .4;
}
.tabs {
  max-width: 520px;
  position: relative;
}
.balance-and-btn {
  height: 100px;
  max-width: 520px;
  display: flex;
  justify-content: space-between;
}
.btn-group{
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
}
.send-btn{
  text-align: center;
  margin-left: 24px;
  cursor: pointer;
  width: 50px;
}
.send-btn:hover {
  .send-btn--body{
    background-color: #e64446;
  }
}
.send-btn:hover .send-btn--text{
  color: #e64446;
}
.send-btn--body{
  width: 38px;
  height: 38px;
  border-radius: 50%;
  background: #053e5e;
  color: #fff;
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}
.new-card-btn{
  align-items: center;
  margin-left: 24px;
  cursor: pointer;
  width: 50px;
}
.new-card-btn:hover .send-btn--text{
  color: #e64446;
}
.new-card-btn:hover .new-card--body{
  background-color: transparent;
  border-color: #e64446;
  color: #e64446;
}
.new-card--body{
  width: 38px;
  height: 38px;
  border-radius: 50%;
  font-size: 24px;
  border: 1px solid #053e5e;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}
.send-btn--text{
  font-size: 12.5px;
  margin-top: 13px;
}
.slide-items {
  position: absolute;
  right: 0;
  height: 100%;
  display: flex;
  align-items: center;
}
ul.slick-dots {
  position: absolute;
  left: -34px;
  width: 11px;
  top: 0;
  height: 100%;
  display: flex!important;
  align-items: center;
  flex-wrap: wrap;
  padding: 25% 0;
  text-align: center;
}
ul.slick-dots li button:before{
  display: none;
}
ul.slick-dots li button{
  width: 8px;
  height: 8px;
  border-radius: 50%;
  opacity: 0.2;
  background-color: #053e5e;
  cursor: pointer;
  margin: 0;
  padding: 0;
}
ul.slick-dots li.slick-active button{
  width: 10px;
  height: 10px;
  background-color: #e64446;
  opacity: 1;
}
.slick-arrow.slick-prev{
  display: none!important;
}
.slick-arrow.slick-next{
  bottom: 0;
  left: 0;
  right: 0;
  height: 90px;
  top: auto;
  width: 100%;
  transform: translate(0, 0);
}
.slick-arrow.slick-next:before {
  display: none;
}
ul.slick-dots li button:hover{
  opacity: 0.5;
  background-color: #053e5e;
}
ul.slick-dots li {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin: 0 auto;
}
ul.slick-dots li:not(.slick-active){
  margin-left: 2px;
}
.card-slick-slider{
  height: 280px;
}
.card-slick-slider svg{
  overflow: visible;
}
.card-slick-slider .slick-list{
  height: 280px!important;
}
.slide-items .slide-items-group div{
  width: 8px;
  height: 8px;
  border-radius: 50%;
  opacity: 0.2;
  background-color: #053e5e;
  cursor: pointer;
  margin: 14px auto 0;
}
.slide-items .slide-items-group div:hover{
  opacity: 0.5;
  background-color: #053e5e;
}
.slide-items .slide-items-group div.active {
  width: 10px;
  height: 10px;
  background-color: #e64446;
  opacity: 1;
}
.share{
  position: absolute;
  font-size: 24px;
  right: 0;
  top: 51px;
  cursor: pointer;
  z-index: 1;
}
.tab-content {
  display: none;
}
.tab-content.active{
  display: block;
}
.card-scroll{
  height: 280px;
  overflow-y: auto;
}
.card-hover{
  position: relative;
  cursor: pointer;
}
.card-hover:hover foreignObject{
  display: block;
}
.card-hover foreignObject{
  display: none;
}
.card-hover-action {
  box-shadow: 0 2px 23px 0 rgba(6, 6, 6, 0.17);
  background: #ffffff;
  position: absolute;
  right: 0;
  top: 10px;
  width: 121px;
  border-radius: 5px;
}
.card-hover-action li {
  height: 39px;
  cursor: pointer;
  padding: 0 15px;
  line-height: 39px;
  font-size: 16px;
}
.card-hover-action li:first-child:hover {
  background-color: rgba(224, 234, 239, .5);
  color: #053e5e;
}
.card-hover-action li:hover {
  color: #e64446;
  background-color: rgba(230, 68, 70, .1);
}
.card-hover-action--arrow {
  border: 6px solid transparent;
  border-bottom-color: #fff;
  border-top: 0;
  position: absolute;
  right: 11px;
  top: -6px;
}

@media screen and (min-width: 1750px)  {
  .container{
    width: 100%;
    max-width: 1120px;
  }
  .row-box .row-box--right{
    width: 320px;
    min-width: 320px;
  }
  .card-slick-slider svg{
    width: 320px;
    height: 220px;
  }
  .tabs, .box-data, .balance-and-btn{
    max-width: 620px;
  }
  .navbar--left .main-menu{
    width: 204px;
  }
  .row-box .row-box--left{
    max-width: 680px;
  }
  .navbar--left .main-menu .logo{
    background: 50% 50% url(./img/icons/logo-full.svg) no-repeat;
  }
  .navbar--left .main-menu .logo .icon{
    display: none;
  }
  .navbar--left .sub-menu, .navbar--left .sub-menu .scroll-y {
    width: 400px;
  }
  .navbar--left .main-menu li a{
    width: 204px;
    justify-content: end;
  }
  .navbar--left .main-menu li a .icon{
    margin-left: 28px;
  }
  .navbar--left .main-menu li a .main-menu-text{
    display: inline;
    margin-left: 20px;
    font-size: 17px;
    color: rgba(5, 62, 94, .4);
  }
  .navbar--left .main-menu li a:hover .main-menu-text{
    color: rgba(5, 62, 94, 1);
  }
  .navbar--left .main-menu li a.active .main-menu-text{
    color: #e64446;
  }
  .slick-arrow.slick-next{
    height: 50px;
  }
}