@import "../../css/colors.scss";
.input-select__single-value{
  width: 100%;
  display: flex;
  align-items: center;
  height: 100%;
}
.container-send{
  margin-top: 16px;
  display: flex;
  margin-left: 24px;
  .input-select{
    width: 100%;
    height: 100%;
  }
  &__account {
    width: 720px;
    margin-right: 8px;
    display: flex;
    align-items: center;
  }
  &__account-title{
    font-size: 16px;
    margin-bottom: 16px;
  }
  &__payments-sign {
    width: 370px;
  }
  &__select-box {
    width: 100%;
    height: 98px;
    .ico-bg{
      width: 24px;
      height: 24px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: rgba(0, 0, 0, 0.2);
    }
    .custom-option {
      display: flex;
    }
    .input-select {
      &__name{
        flex: 1;
        font-size: 16px;
        margin-left: 8px;
        span{
          font-size: 12px;
        }
      }
      &__label {
        flex: 1;
        font-size: 12px;
        text-align: right;
        color: rgba($marine, .5);
      }
    }
  }
  &__ivoice-box {
    min-width: 280px;
    margin-right: 41px;
    margin-left: 70px;
    text-align: center;
  }
  &__ivoice-name{
    font-size: 12.5px;
    color: rgba($marine, .67);
    margin-bottom: 16px;
  }
  &__payments-title{
    font-size: 16px;
    font-weight: 600;
    color: rgba($marine, .67);
  }
  .open-select{
    transform: rotate(180deg);
  }
  &__payments-list {
    .payments-list-item{
      height: 56px;
      display: flex;
      align-items: center;
      margin-top: 15px;
      &__title {
        font-size: 16px;
        span{
          font-size: 12px;
        }
      }
      &__label{
        font-size: 12.5px;
        color: rgba($marine, .45);
      }
      &__icon{
        margin-right: 16px;
        width: 32px;
        height: 32px;
        background-color: #e0eaef;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
}
.box-own-payees{
  margin-left: 24px;
  margin-top: 20px;
  .payees-list {
    margin-top: -27px;
    padding-top: 16px;
    background-color: #fff;
    &__name{
      flex: 1;
    }
    &__id{
      flex: 1;
    }
    &__ico {
      width: 32px;
      height: 32px;
      background-color: #f7f7ed;
      margin-right: 12px;
      margin-left: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    li {
      height: 56px;
      border-bottom: 1px solid #cdd8df;
      display: flex;
      align-items: center;
  
    }
  }
}
